@import url(https://fonts.googleapis.com/css2?family=Charm&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montez&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ultra&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Niconne&display=swap);
* {
  box-sizing: border-box;
}

:root {
  --bg-nav: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%); 
  --ff-txt-bar:'Charm', cursive;
  --ff-txt-hand-rw1:'Montez', cursive;

  --ff-txt-hand-rw2: 'Niconne', cursive;
  --ff-txt-portfolio:'Ultra', serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Charm', cursive;
  font-family: var(--ff-txt-bar);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
@media only screen and (max-width: 780px) {
  #st {
    
    position: -webkit-sticky;
    
    position: sticky;
    top: 4rem;
  }
  }
.Home {
  width:100%;
  height: 100vh;
  background-image:url(/pf/static/media/bluedesk.1240c110.jpg);
  background-size: cover; 
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 50%;
  
}
.txtH {
  /* text-align: center; */
  font-size: clamp(2rem,5vw, 5rem);
  padding-top: 30%;
  font-family: var(--ff-txt-portfolio);
  color:#0000FF;

}
span {
  color:red;
}


.About {
    
    height: 100vh;
    background-image:url(/pf/static/media/desk.6186dfad.jpg);
    background-position-x: 20%;
    background-position-y: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    /* line-height: 100vh; */

}

.txtA {
  font-family: var(--ff-txt-hand-rw1);
  font-size: clamp(3vh,4rem, 4vw);
  line-height: 1.5em;
  color:  #ffffff;
  text-align: center;

}

.Project {
  
    height: 100vh;
    background: linear-gradient(
    100deg,
    #b0eae8 30%,
