.Fix {
    min-height: 100vh;
    width: 13rem;
    margin: 0;
    padding: 0;
    background: var(--bg-nav); 
    
}

.name {
    font-family: var(--ff-txt-bar);
}

.underline {
    font-family: var(--ff-txt-bar);
    text-decoration: none;
    display: inline-block;
    vertical-align: top;
    line-height: 30px;
    color: white;
    text-align: center;
    font-size: 1.4rem;
    margin: 10px;
    position: relative;
}

.Photo {
    width: 75%;
    border-radius: 50%;
}

.NavbarTop {
    position: relative;
    height: 4rem;
    background: var(--bg-nav); 
}

.dropdown-menu {
